html {
  background-color: #000000;
}

.page {
  min-width: 320px;
  max-width: 1280px;
  width: 100%;
  min-height: 100vh;
  margin: auto;
  color: #ffffff;
  font-weight: 400;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
}

.header {
  max-width: 880px;
  min-height: 74px;
  margin: 45px auto 36.25px auto;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
}

@media screen and (max-width: 960px) {
  .header {
    max-width: 70%;
  }
}

@media screen and (max-width: 600px) {
  .header {
    max-width: 100%;
    min-height: 56px;
    margin-top: 0;
    margin-bottom: 38.25px;
  }

  .header__image {
    max-width: 126px;
    min-height: 24px;
    margin: 28px 167px 32px 27px;
  }
}

.profile {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 880px;
  margin: 0 auto 50px auto;
  display: flex;
  justify-content: space-between;
}

.profile__user {
  max-width: 583px;
  display: flex;
  position: relative;
}

.profile__image {
  background-size: cover;
  background-position: center;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.profile__change-image-button {
  width: 26px;
  height: 26px;
  background: transparent;
  border: none;
  position: absolute;
  left: 47px;
  bottom: 56px;
}

.profile__change-image-button-hide {
  display: none;
}

.profile__image:hover {
  opacity: 0.8;
}

.profile__image:hover > .profile__change-image-button-hide {
  display: block;
}

.profile__info {
  max-width: 415px;
  margin: 22px 0px 21px 26.25px;
  display: grid;
  grid-template-columns: minmax(373px, auto) auto;
  grid-template-areas:
    "name button"
    "occupation occupation";
}

.profile__title {
  grid-area: name;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 373px;
  margin: 0 0 16px 0;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
}

.profile__edit-button {
  grid-area: button;
  width: 24px;
  height: 24px;
  background: #000000;
  text-decoration: none;
  border: 1px solid #ffffff;
  cursor: pointer;
  margin-top: 15px;
  margin-left: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.profile__edit-button:hover {
  opacity: 0.6;
}

.profile__subtitle {
  grid-area: occupation;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 415px;
  margin-top: 0;
  margin-bottom: 0;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 21.78px;
}

.profile__add-button {
  width: 150px;
  height: 50px;
  background: #000000;
  border: 2px solid #ffffff;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: center;
}

.profile__add-button:hover {
  opacity: 0.6;
}

@media screen and (max-width: 960px) {
  .profile {
    max-width: 70%;
    flex-direction: column;
    justify-content: center;
  }

  .profile__user {
    flex-direction: column;
  }

  .profile__image {
    margin: 0 auto 26.25px auto;
  }

  .profile__info {
    max-width: 80%;
    margin: 0 auto;
    grid-template-columns: minmax(196px, auto) 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 14px;
    grid-template-areas:
      "name button"
      "occupation occupation";
  }

  .profile__title {
    margin-bottom: 0;
    max-width: 373px;
  }

  .profile__edit-button {
    margin: 0;
    align-self: center;
  }

  .profile__subtitle {
    margin-bottom: 36px;
    text-align: center;
    max-width: 415px;
  }

  .profile__add-button {
    width: 80%;
    margin: auto;
  }

  .profile__change-image-button {
    left: 280px;
    top: 50px;
  }
}

@media screen and (max-width: 768px) {
  .profile__change-image-button {
    left: 255px;
    top: 50px;
  }
}

@media screen and (max-width: 740px) {
  .profile__title {
    max-width: 196px;
    font-size: 22px;
    line-height: 27px;
  }
  .profile__change-image-button {
    left: 245px;
  }
}

@media screen and (max-width: 600px) {
  .profile {
    max-width: 282px;
    margin-bottom: 36px;
  }

  .profile__info {
    max-width: 282px;
    margin: 0 auto;
    grid-template-columns: minmax(196px, auto) 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 14px;
    grid-template-areas:
      "name button"
      "occupation occupation";
  }

  .profile__edit-button {
    width: 18px;
    height: 18px;
  }

  .profile__subtitle {
    max-width: 282px;
    font-size: 14px;
    line-height: 16.94px;
  }

  .profile__add-button {
    width: 282px;
  }

  .profile__change-image-button {
    left: 130px;
  }
}

.cards {
  max-width: 880px;
  margin: 0 auto 68px auto;
}

.cards__gallery {
  list-style-type: none;
  margin: 0 auto;
  padding-left: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 20px 17px;
  gap: 20px 17px;
}

@media screen and (max-width: 1024px) {
  .cards {
    margin: 0 auto 50px auto;
  }
}

@media screen and (max-width: 960px) {
  .cards {
    max-width: 80%;
  }

  .cards__gallery {
    width: 100%;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 17px 14px;
    gap: 17px 14px;
  }
}

@media screen and (max-width: 740px) {
  .cards__gallery {
    grid-template-columns: repeat(1, 1fr);
    grid-row-gap: 20px;
  }
}

@media screen and (max-width: 600px) {
  .cards {
    max-width: 90%;
  }
}

.card {
  min-width: 282px;
  max-height: 361px;
  background-color: #ffffff;
  border-radius: 10px;
  position: relative;
}

.card__image {
  max-width: 100%;
  height: 282px;
  border-radius: 10px 10px 0 0;
  background-size: cover;
  background-position: center;
}

.card__image:hover {
  cursor: pointer;
}

.card__delete-button {
  background: transparent url("/src/images/bin.svg") center no-repeat;
  border: none;
  position: absolute;
  top: 18px;
  left: 250.28px;
  cursor: pointer;
  width: 18px;
  height: 19.3px;
}

.card__delete-button:hover {
  background-image: url("/src/images/bin_hover.svg");
}

.card__info {
  max-width: 246px;
  margin: auto 15px auto 21px;
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  color: #000000;
}
.card__title {
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 900;
  font-size: 24px;
  line-height: 29.05px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.card__like-button {
  width: 21px;
  height: 19px;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
  background-image: url("/src/images/heart.svg");
}

.card__like-button:hover {
  opacity: 0.5;
}

.card__like-button_active {
  background-image: url("/src/images/heart-active.svg");
}

.card__likes {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (max-width: 960px) {
  .card {
    justify-self: center;
  }
}

.footer {
  max-width: 318px;
  margin: 0 60% 5% 16%;
}

.footer__copyright {
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 21.78px;
  color: #545454;
  margin-top: 0;
  margin-bottom: 0;
}

@media screen and (max-width: 1024px) {
  .footer {
    max-width: 25%;
    margin: 0 60% 5% 7%;
  }
}

@media screen and (max-width: 960px) {
  .footer {
    margin: 0 60% 5% 15%;
  }
}

@media screen and (max-width: 600px) {
  .footer {
    max-width: 90%;
    margin: 0 auto 36px auto;
  }

  .footer__copyright {
    font-size: 14px;
    line-height: 16.94px;
  }
}

.popup {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  pointer-events: none;
  opacity: 0;
  transition: visibility 0s linear 0.3s, opacity 0.3s linear;
}

.popup_open {
  visibility: visible;
  pointer-events: all;
  opacity: 1;
  transition-delay: 0s;
}

.popup__container {
  position: relative;
}

.popup__close-button {
  background-image: url("/src/images/Close_Icon.svg");
  background-repeat: no-repeat;
  background-color: transparent;
  width: 40px;
  height: 40px;
  border: none;
  position: absolute;
  left: 430px;
  bottom: 330px;
  cursor: pointer;
}

.popup__close-button:hover {
  opacity: 0.6;
}

.popup__form {
  width: 430px;
  height: 330px;
  background: #ffffff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.popup__form-title {
  max-width: 330px;
  margin: 34px 0 0 36px;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 900;
  font-size: 24px;
  line-height: 29.05px;
  color: #000000;
}

.popup__form-fieldset {
  padding: 0;
  margin: 0 auto;
  width: 358px;
  border: none;
}
.popup__form-input {
  width: 100%;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: black;
  outline: #ffffff;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.popup__form-button {
  width: 100%;
  padding: 13.66px 124px 18.49px 125px;
  box-sizing: border-box;
  cursor: pointer;
  color: #ffffff;
  background: #000000;
  border: 0;
  border-radius: 2px;
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
}

.popup__form-button:hover {
  background: rgba(0, 0, 0, 0.8);
}

.popup__form-button_disabled {
  opacity: 0.2;
  pointer-events: none;
}

.popup__container_content_preview {
  background: transparent;
  width: auto;
  height: auto;
}

.popup__image-content {
  margin: 0;
  position: relative;
}

.popup__close-button_close_preview {
  left: 100%;
  bottom: 100%;
}

.popup__preview-image {
  object-fit: cover;
  max-width: 75vw;
  max-height: 75vh;
}

.popup__preview-caption {
  max-width: 291px;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
}

.popup__form-input_type_error {
  border: 1px solid #ff0000;
}

.popup__form-error {
  visibility: hidden;
  display: none;
  color: #ff0000;
  font-size: 12px;
}

.popup__form-error_visible {
  visibility: visible;
  display: block;
}

.popup__form_type_confirm-delete {
  height: 181px;
  justify-content: space-evenly;
}

.popup__form-title_confirm-delete {
  max-width: 358px;
  margin: 0 36px;
}

.popup__close-button_close_confirm-delete {
  bottom: 181px;
}

@media screen and (max-width: 600px) {
  .popup__close-button {
    background-size: contain;
    width: 20px;
    height: 20px;
    left: 90%;
    bottom: 335px;
  }

  .popup__form {
    width: 282px;
    height: 322px;
  }

  .popup__form-title {
    max-width: 238px;
    margin: 25px 0 0 22px;
    font-size: 18px;
    line-height: 21.78px;
  }

  .popup__form-fieldset {
    width: 238px;
  }

  .popup__form-button {
    width: 238px;
    padding: 14px 100px;
    font-size: 14px;
    line-height: 16.94px;
  }

  .popup__preview-caption {
    max-width: 240px;
  }

  .popup__close-button_close_preview {
    left: 100%;
    bottom: 100%;
  }
}



